import React, { Component } from "react";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import { connect } from "react-redux";
import Slika from "../../assets/global_shopping_experience.gif";
import Card from "../../Layout/Card/Card";
import withWidth from "@material-ui/core/withWidth";
import Styles from "./styles";
import SearchBarComponent from "./SearchBar/SearchBar";
import CheckBoxComponent from "./CheckBox";
import ViewStream from "@material-ui/icons/ViewStream";
import LayoutButtons from "./LayoutButtons";
import { getProizvodi } from "../../redux/actions/proizvodiActions";
const styles = Styles;
class MainContent extends Component {
  state = {
    filters: false,
    sortBy: false,
    layout: 12
  };
  changeFiltersState = () => {
    this.setState(prevState => ({
      filters: !prevState.filters
    }));
  };
  changeSortByState = () => {
    this.setState(prevState => ({
      sortBy: !prevState.sortBy
    }));
  };
  componentDidUpdate() {
    if (this.props.width === "xs" && this.state.layout !== 12) {
      this.setState({ layout: 12 });
    }
  }
  changeLayout = number => {
    if (number !== this.state.layout) this.setState({ layout: number });
  };
  search = e => {
    this.props.getProizvodi({ search: e });
  };
  componentDidMount() {
    this.props.getProizvodi();
  }
  render() {
    const { classes, width, proizvodi } = this.props;
    const { filters, sortBy, layout } = this.state;
    return (
      <Grid
        container
        alignContent="flex-start"
        alignItems="flex-start"
        justify="center"
        className={width === "xs" ? classes.rootMobile : classes.rootDesktop}
      >
        <Grid container justify="center">
          <Grid item container justify="center" xs={12}>
            <SearchBarComponent search={this.search} />
          </Grid>

          <CheckBoxComponent
            changeSortByState={this.changeSortByState}
            filters={filters}
            sortBy={sortBy}
            changeFiltersState={this.changeFiltersState}
          />
        </Grid>
        {width === "xs" ? null : (
          <LayoutButtons changeLayout={this.changeLayout} />
        )}
        {proizvodi.length > 0 ? (
          proizvodi.map(({ naslov, slike, cijena, tezina }) => (
            <Grid item container justify="center" xs={layout}>
              <Card
                naslov={naslov}
                slika={slike[0]}
                cijena={cijena}
                tezina={tezina}
              />
            </Grid>
          ))
        ) : (
          <img className={classes.slika} src={Slika} alt="aloo" />
        )}
      </Grid>
    );
  }
}

const mapStateToProps = state => ({
  proizvodi: state.proizvodi.proizvodi
});

const mapDispatchToProps = {
  getProizvodi
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withWidth()(withStyles(styles)(MainContent)));
