import React from "react";
import Checkbox from "@material-ui/core/Checkbox";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
const CheckBoxComponent = ({
  changeFiltersState,
  changeSortByState,
  filters,
  sortBy
}) => {
  return (
    <FormGroup row>
      <FormControlLabel
        control={
          <Checkbox
            checked={filters}
            onChange={() => changeFiltersState()}
            value="checkedA"
          />
        }
        label="Show filters"
      />
      <FormControlLabel
        control={
          <Checkbox
            checked={sortBy}
            onChange={() => changeSortByState()}
            value="checkedB"
            color="primary"
          />
        }
        label="Show sortBy"
      />
    </FormGroup>
  );
};

export default CheckBoxComponent;
