import React, { Component } from "react";
import { Grid, Button, Badge, MenuItem } from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";
import { connect } from "react-redux";
import { reduxForm, Field } from "redux-form";
import DropZoneComponent from "./DropZone";
import Form from "./Form";
import myStyles from "./styles";
import SnackBar from "../../Layout/SnackBar/SnackBar";
import {
  errorPostProizvod,
  newProizvod
} from "../../redux/actions/proizvodiActions";
const styles = theme => myStyles(theme);
class NoviProizvod extends Component {
  state = {
    slike: []
  };
  drop = files => {
    this.setState(prevState => ({
      slike: [...prevState.slike, ...files]
    }));
  };
  obrisi = id => () => {
    this.setState(prevState => ({
      slike: prevState.slike.filter(slika => slika.preview !== id)
    }));
  };
  prikaziSlike = () => {
    return this.state.slike.map(slika => (
      <div key={slika.preview} style={{ margin: "0.3rem" }}>
        <Badge
          badgeContent={"x"}
          color="secondary"
          style={{ cursor: "pointer" }}
          onClick={this.obrisi(slika.preview)}
        >
          <img height="50" width="50" src={slika.preview} alt="slika" />
        </Badge>
      </div>
    ));
  };
  componentDidUpdate() {
    if (
      Object.keys(this.props.newProizvodState).length > 0 &&
      this.state.slike.length > 0
    )
      this.setState({ slike: [] });
  }
  snackRemoveError = () => {
    this.props.errorPostProizvod(false);
  };
  snackRemoveSuccess = () => {
    this.props.newProizvod({});
  };
  componentWillUnmount() {
    this.props.errorPostProizvod(false);
    this.props.newProizvod({});
  }
  render() {
    const { classes, errorServer, newProizvodState } = this.props;
    const { slike } = this.state;

    return (
      <Grid
        className={classes.root}
        container
        justify="center"
        alignItems="center"
        alignContent="center"
      >
        <DropZoneComponent drop={this.drop} />

        {slike.length > 0 ? (
          <Grid
            container
            alignItems="center"
            justify="center"
            direction="row"
            item
          >
            {this.prikaziSlike()}
          </Grid>
        ) : null}
        <Form submit={this.submit} pictures={this.state.slike} />
        {errorServer && (
          <SnackBar
            variantSnackBar="error"
            poruka={errorServer}
            open={true}
            snackRemove={this.snackRemoveError}
          />
        )}
        {newProizvodState.naslov && (
          <SnackBar
            variantSnackBar="success"
            poruka={`Upsjesno ste objavili proizvod: ${
              newProizvodState.naslov
            }`}
            open={true}
            snackRemove={this.snackRemoveSuccess}
          />
        )}
      </Grid>
    );
  }
}
const mapStateToProps = state => ({
  newProizvodState: state.proizvodi.newProizvod,
  errorServer: state.proizvodi.error
});

const mapDispatchToProps = {
  errorPostProizvod,
  newProizvod
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(reduxForm({ form: "NoviProizvod" })(withStyles(styles)(NoviProizvod)));
