import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import FavoriteBorder from "@material-ui/icons/FavoriteBorder";
import IconButton from "@material-ui/core/IconButton";
const styles = {
  card: {
    maxWidth: 400,
    margin: "1rem",
    marginTop: 0
  },
  media: {
    // ⚠️ object-fit is not supported by IE11.
    objectFit: "cover"
  },
  button: {
    color: "transparent",
    background: "linear-gradient(to right, #373b44, #4286f4)",
    "-webkit-background-clip": "text",
    fontFamily: "Kalam",
    fontSize: "1.3rem"
  },
  content: {
    fontFamily: "Kalam"
  },
  naslov: {
    paddingBottom: 0,
    textAlign: "center"
  },
  icona: {
    fontSize: "2rem"
  },
  iconButton: {
    padding: 5
  }
};

function ImgMediaCard(props) {
  const { classes, naslov, cijena, slika, tezina } = props;
  return (
    <Card className={classes.card}>
      <CardActionArea onClick={() => console.log("alo")}>
        <CardMedia
          component="img"
          alt="slika"
          className={classes.media}
          height="250"
          image={slika}
          title="slika"
        />
        <CardContent classes={{ root: classes.naslov }}>
          <Typography
            noWrap
            className={classes.content}
            gutterBottom
            variant="h5"
            component="h2"
          >
            {naslov}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Typography className={classes.button}>{tezina} KG</Typography>

        <Typography className={classes.button} style={{ marginLeft: "auto" }}>
          {cijena} KM
        </Typography>
      </CardActions>
    </Card>
  );
}

ImgMediaCard.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(ImgMediaCard);
