import React from "react";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import InboxIcon from "@material-ui/icons/MoveToInbox";
import DraftsIcon from "@material-ui/icons/Drafts";
import StarIcon from "@material-ui/icons/Star";
import SendIcon from "@material-ui/icons/Send";
import HomeIcon from "@material-ui/icons/Home";
import Divider from "@material-ui/core/Divider";

import { Link } from "react-router-dom";
import { withStyles } from "@material-ui/core/styles";
const styles = {
  text: {
    color: "white"
  }
};

const ListComponent = ({ classes }) => {
  const { text } = classes;

  return (
    <List>
      <ListItem>
        <ListItemText classes={{ primary: text }} primary="Home" />
      </ListItem>
      <ListItem button component={Link} to="/Pocetna">
        <ListItemIcon classes={{ root: text }}>
          <HomeIcon />
        </ListItemIcon>
        <ListItemText classes={{ primary: text }} primary="Home" />
      </ListItem>
      <Divider />
      <ListItem>
        <ListItemText classes={{ primary: text }} primary="Other" />
      </ListItem>
      <ListItem button>
        <ListItemIcon classes={{ root: text }}>
          <StarIcon />
        </ListItemIcon>
        <ListItemText classes={{ primary: text }} primary="Starred" />
      </ListItem>
      <ListItem button component={Link} to="/noviProizvod">
        <ListItemIcon classes={{ root: text }}>
          <SendIcon />
        </ListItemIcon>
        <ListItemText classes={{ primary: text }} primary="Send mail" />
      </ListItem>
      <ListItem button>
        <ListItemIcon classes={{ root: text }}>
          <DraftsIcon />
        </ListItemIcon>
        <ListItemText primary="Drafts" classes={{ primary: text }} />
      </ListItem>
    </List>
  );
};

export default withStyles(styles)(ListComponent);
